/**
 * API
 * 
 */

// 
let networkStatus = {
  isConnected: true,
  networkType: 'unknown'
};

// 
const requestQueue = new Map();
const retryQueue = new Map();

// 
const apiCache = new Map();
const CACHE_DURATION = 5 * 60 * 1000; // 5

/**
 * 
 */
export function initNetworkMonitor() {
  try {
    // 
    uni.onNetworkStatusChange((res) => {
      networkStatus.isConnected = res.isConnected;
      networkStatus.networkType = res.networkType;
      
      console.log('[] :', networkStatus);
      
      if (res.isConnected) {
        // 
        retryFailedRequests();
      }
    });
    
    // 
    uni.getNetworkType({
      success: (res) => {
        networkStatus.networkType = res.networkType;
        networkStatus.isConnected = res.networkType !== 'none';
      }
    });
    
    console.log('[API] ');
  } catch (error) {
    console.error('[API] :', error);
  }
}

/**
 * API
 * @param {Function} apiCall - API
 * @param {Object} options - 
 * @returns {Promise} - Promise
 */
export async function enhancedApiCall(apiCall, options = {}) {
  const {
    timeout = 10000,
    retryCount = 3,
    retryDelay = 1000,
    cacheKey = null,
    cacheDuration = CACHE_DURATION,
    fallbackData = null,
    showLoading = false,
    loadingText = '...',
    showError = true,
    errorMessage = '',
    requireNetwork = true
  } = options;
  
  const requestId = generateRequestId();
  
  try {
    // 
    if (showLoading) {
      uni.showLoading({
        title: loadingText,
        mask: true
      });
    }
    
    // 
    if (requireNetwork && !networkStatus.isConnected) {
      throw new Error('');
    }
    
    // 
    if (cacheKey) {
      const cachedData = getCachedData(cacheKey);
      if (cachedData) {
        console.log('[API] :', cacheKey);
        if (showLoading) uni.hideLoading();
        return cachedData;
      }
    }
    
    // 
    if (requestQueue.has(requestId)) {
      console.log('[API] :', requestId);
      return await requestQueue.get(requestId);
    }
    
    // API
    const requestPromise = executeApiCallWithRetry(
      apiCall, 
      retryCount, 
      retryDelay, 
      timeout
    );
    
    requestQueue.set(requestId, requestPromise);
    
    const result = await requestPromise;
    
    // 
    if (cacheKey && result) {
      setCachedData(cacheKey, result, cacheDuration);
    }
    
    // 
    requestQueue.delete(requestId);
    
    if (showLoading) uni.hideLoading();
    
    return result;
    
  } catch (error) {
    console.error('[API] API:', error);
    
    // 
    requestQueue.delete(requestId);
    
    if (showLoading) uni.hideLoading();
    
    // 
    return handleApiError(error, {
      showError,
      errorMessage,
      fallbackData,
      cacheKey
    });
  }
}

/**
 * API
 */
async function executeApiCallWithRetry(apiCall, retryCount, retryDelay, timeout) {
  let lastError;
  
  for (let attempt = 0; attempt <= retryCount; attempt++) {
    try {
      // Promise
      const timeoutPromise = new Promise((_, reject) => {
        setTimeout(() => reject(new Error('')), timeout);
      });
      
      // API
      const result = await Promise.race([
        apiCall(),
        timeoutPromise
      ]);
      
      console.log(`[API] API ( ${attempt + 1}/${retryCount + 1})`);
      return result;
      
    } catch (error) {
      lastError = error;
      console.warn(`[API] API ( ${attempt + 1}/${retryCount + 1}):`, error.message);
      
      // 
      if (attempt < retryCount) {
        await delay(retryDelay * Math.pow(2, attempt)); // 
      }
    }
  }
  
  throw lastError;
}

/**
 * API
 */
function handleApiError(error, options) {
  const { showError, errorMessage, fallbackData, cacheKey } = options;
  
  // 
  if (cacheKey) {
    const expiredCache = getCachedData(cacheKey, true);
    if (expiredCache) {
      console.log('[API] :', cacheKey);
      if (showError) {
        uni.showToast({
          title: '',
          icon: 'none',
          duration: 2000
        });
      }
      return expiredCache;
    }
  }
  
  // 
  if (showError) {
    const message = getErrorMessage(error) || errorMessage;
    uni.showToast({
      title: message,
      icon: 'none',
      duration: 3000
    });
  }
  
  // 
  if (fallbackData !== null) {
    console.log('[API] ');
    return fallbackData;
  }
  
  // 
  throw error;
}

/**
 * 
 */
function getErrorMessage(error) {
  if (!error) return '';
  
  // 
  if (error.message?.includes('timeout') || error.message?.includes('')) {
    return '';
  }
  
  if (error.message?.includes('network') || error.message?.includes('')) {
    return '';
  }
  
  // HTTP
  if (error.statusCode) {
    switch (error.statusCode) {
      case 400: return '';
      case 401: return '';
      case 403: return '';
      case 404: return '';
      case 500: return '';
      case 502: return '';
      case 503: return '';
      default: return ` (${error.statusCode})`;
    }
  }
  
  // 
  if (error.code) {
    return error.message || '';
  }
  
  return error.message || '';
}

/**
 * 
 */
function getCachedData(key, allowExpired = false) {
  try {
    const cached = apiCache.get(key);
    if (!cached) return null;
    
    const now = Date.now();
    if (!allowExpired && now > cached.expireTime) {
      apiCache.delete(key);
      return null;
    }
    
    return cached.data;
  } catch (error) {
    console.error('[API] :', error);
    return null;
  }
}

function setCachedData(key, data, duration) {
  try {
    apiCache.set(key, {
      data,
      expireTime: Date.now() + duration,
      createTime: Date.now()
    });
  } catch (error) {
    console.error('[API] :', error);
  }
}

/**
 * 
 */
export function clearExpiredCache() {
  const now = Date.now();
  for (const [key, cached] of apiCache.entries()) {
    if (now > cached.expireTime) {
      apiCache.delete(key);
    }
  }
}

/**
 * 
 */
function retryFailedRequests() {
  console.log('[API] ');
  
  for (const [requestId, requestInfo] of retryQueue.entries()) {
    setTimeout(() => {
      enhancedApiCall(requestInfo.apiCall, requestInfo.options)
        .then(result => {
          console.log('[API] :', requestId);
          retryQueue.delete(requestId);
        })
        .catch(error => {
          console.error('[API] :', requestId, error);
        });
    }, Math.random() * 2000); // 
  }
}

/**
 * 
 */
export async function preloadData(preloadConfigs) {
  console.log('[API] ');
  
  const preloadPromises = preloadConfigs.map(async (config) => {
    try {
      await enhancedApiCall(config.apiCall, {
        ...config.options,
        showLoading: false,
        showError: false
      });
      console.log('[API] :', config.key);
    } catch (error) {
      console.warn('[API] :', config.key, error);
    }
  });
  
  await Promise.allSettled(preloadPromises);
  console.log('[API] ');
}

/**
 * API
 */
export async function batchApiCall(apiCalls, options = {}) {
  const {
    concurrency = 3,
    failFast = false,
    showProgress = false
  } = options;
  
  if (showProgress) {
    uni.showLoading({
      title: ` 0/${apiCalls.length}`,
      mask: true
    });
  }
  
  const results = [];
  let completed = 0;
  
  // 
  for (let i = 0; i < apiCalls.length; i += concurrency) {
    const batch = apiCalls.slice(i, i + concurrency);
    
    const batchPromises = batch.map(async (apiCall, index) => {
      try {
        const result = await enhancedApiCall(apiCall.fn, apiCall.options);
        completed++;
        
        if (showProgress) {
          uni.showLoading({
            title: ` ${completed}/${apiCalls.length}`,
            mask: true
          });
        }
        
        return { success: true, data: result, index: i + index };
      } catch (error) {
        completed++;
        
        if (showProgress) {
          uni.showLoading({
            title: ` ${completed}/${apiCalls.length}`,
            mask: true
          });
        }
        
        if (failFast) throw error;
        return { success: false, error, index: i + index };
      }
    });
    
    const batchResults = await Promise.all(batchPromises);
    results.push(...batchResults);
  }
  
  if (showProgress) {
    uni.hideLoading();
  }
  
  return results;
}

/**
 * 
 */
function generateRequestId() {
  return `req_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`;
}

function delay(ms) {
  return new Promise(resolve => setTimeout(resolve, ms));
}

/**
 * API
 */
export const apiStatusMixin = {
  data() {
    return {
      apiLoading: false,
      apiError: null,
      apiData: null
    };
  },
  
  methods: {
    /**
     * API
     */
    async safeApiCall(apiCall, options = {}) {
      this.apiLoading = true;
      this.apiError = null;
      
      try {
        const result = await enhancedApiCall(apiCall, options);
        this.apiData = result;
        return result;
      } catch (error) {
        this.apiError = error;
        throw error;
      } finally {
        this.apiLoading = false;
      }
    },
    
    /**
     * API
     */
    resetApiStatus() {
      this.apiLoading = false;
      this.apiError = null;
      this.apiData = null;
    }
  }
};

// 
export const defaultApiOptions = {
  timeout: 10000,
  retryCount: 2,
  retryDelay: 1000,
  showLoading: false,
  showError: true
};

console.log('[API] ');