/**
 * WHO-5 
 * @param {Array} answers - 0-5
 * @returns {Object} 
 */
function scoreWHO5(answers) {
  const total = answers.reduce((sum, answer) => sum + answer, 0)
  const transformed = total * 4 // 0-100
  
  let alerts = []
  if (transformed <= 50) {
    alerts.push('')
  }
  
  let suggestions = ''
  if (transformed <= 50) {
    suggestions = ''
  } else {
    suggestions = ''
  }
  
  return {
    total,
    transformed_score: transformed,
    transform_note: '40100',
    alerts,
    suggestions
  }
}

