/**
 * ASQ-4 
 * @param {Array} answers - 0-1
 * @returns {Object} 
 */
function scoreASQ4(answers) {
  const hasPositive = answers.some(answer => answer === 1)
  
  let alerts = []
  if (hasPositive) {
    alerts.push('')
  }
  
  let suggestions = ''
  if (hasPositive) {
    suggestions = '400-161-9995'
  } else {
    suggestions = ''
  }
  
  return {
    total: answers.reduce((sum, answer) => sum + answer, 0),
    isPositive: hasPositive,
    alerts,
    suggestions
  }
}

