// 
import { getToken, clearLoginData } from './auth.js';

// 
class CloudFunctionRequest {
  constructor() {
    this.timeout = 10000;
  }

  /**
   * 
   * @param {string} name - 
   * @param {Object} data - 
   * @param {Object} options - 
   */
  async callFn(name, data = {}, options = {}) {
    const {
      showLoading = false,
      loadingText = '...',
      showToast = true,
      timeout = this.timeout
    } = options;

    console.log(`[CLOUD_FN] : ${name}`, data);

    if (showLoading) {
      uni.showLoading({ title: loadingText });
    }

    try {
      const result = await Promise.race([
        uniCloud.callFunction({ name, data }),
        new Promise((_, reject) => 
          setTimeout(() => reject(new Error('')), timeout)
        )
      ]);

      console.log(`[CLOUD_FN] ${name} :`, result);

      if (showLoading) {
        uni.hideLoading();
      }

      // 
      if (result.result) {
        return result.result;
      } else {
        throw new Error('');
      }

    } catch (error) {
      console.error(`[CLOUD_FN] ${name} :`, error);
      
      if (showLoading) {
        uni.hideLoading();
      }

      if (showToast) {
        uni.showToast({
          title: error.message || '',
          icon: 'none',
          duration: 2000
        });
      }

      throw error;
    }
  }
}

// HTTP 
class HttpRequest {
  constructor() {
    this.timeout = 10000;
    this.baseURL = 'https://your-api-domain.com'; // API
  }

  /**
   * HTTP 
   * @param {string} method - 
   * @param {string} url - 
   * @param {Object} data - 
   * @param {Object} options - 
   */
  async httpRequest(method, url, data = {}, options = {}) {
    const {
      skipAuth = false,
      showLoading = false,
      loadingText = '...',
      showToast = true,
      timeout = this.timeout
    } = options;

    console.log(`[HTTP] ${method} ${url}`, data);

    if (showLoading) {
      uni.showLoading({ title: loadingText });
    }

    return new Promise((resolve, reject) => {
      const timer = setTimeout(() => {
        reject(new Error(''));
      }, timeout);

      const requestOptions = {
        url: this.baseURL + url,
        method: method.toUpperCase(),
        timeout,
        header: {
          'Content-Type': 'application/json'
        }
      };

      // 
      if (!skipAuth) {
        const token = getToken();
        if (token) {
          requestOptions.header.Authorization = `Bearer ${token}`;
        }
      }

      // 
      if (method.toUpperCase() === 'GET') {
        if (Object.keys(data).length > 0) {
          const params = new URLSearchParams(data).toString();
          requestOptions.url += '?' + params;
        }
      } else {
        requestOptions.data = data;
      }

      uni.request({
        ...requestOptions,
        success: async (res) => {
          clearTimeout(timer);
          
          if (showLoading) {
            uni.hideLoading();
          }

          console.log(`[HTTP] ${method} ${url} :`, res);
          
          // 401
          if (res.statusCode === 401 && !skipAuth) {
            console.log('[HTTP] 401');
            clearLoginData();
            
            if (showToast) {
              uni.showToast({
                title: '',
                icon: 'none',
                duration: 2000
              });
            }
            
            // 
            setTimeout(() => {
              uni.navigateTo({ url: '/pages/login/login' });
            }, 500);
            
            reject(new Error(''));
            return;
          }

          // HTTP
          if (res.statusCode >= 200 && res.statusCode < 300) {
            resolve(res.data);
          } else {
            const errorMsg = res.data?.message || `HTTP ${res.statusCode}`;
            reject(new Error(errorMsg));
          }
        },
        fail: (err) => {
          clearTimeout(timer);
          
          if (showLoading) {
            uni.hideLoading();
          }

          console.error(`[HTTP] ${method} ${url} :`, err);
          
          if (showToast) {
            uni.showToast({
              title: err.errMsg || '',
              icon: 'none',
              duration: 2000
            });
          }
          
          reject(err);
        }
      });
    });
  }

  // GET
  get(url, params = {}, options = {}) {
    return this.httpRequest('GET', url, params, options);
  }

  // POST
  post(url, data = {}, options = {}) {
    return this.httpRequest('POST', url, data, options);
  }

  // PUT
  put(url, data = {}, options = {}) {
    return this.httpRequest('PUT', url, data, options);
  }

  // DELETE
  delete(url, data = {}, options = {}) {
    return this.httpRequest('DELETE', url, data, options);
  }
}

// 
const cloudRequest = new CloudFunctionRequest();
const httpRequest = new HttpRequest();

// API
export const authAPI = {
  // 
  wxLogin: async (code) => {
    // 
    const { result } = await uniCloud.callFunction({ 
      name: 'auth-login', 
      data: { code } 
    });
    const r = result || {};
    
    // errCode0data
    const ok = (r && Number(r.errCode) === 0 && r.data && Object.keys(r.data).length > 0);
    
    if (ok) {
      // data
      return r.data;
    } else {
      // 
      const errorMsg = r?.errMsg || '';
      throw new Error(errorMsg);
    }
  },
  
  // 
  getUserInfo: () => cloudRequest.callFn('auth-me', {}, {
    showToast: false
  })
};

export const musicAPI = {
  // 
  getCategories: () => cloudRequest.callFn('fn-music', { action: 'categories' }),
  
  // 
  getList: (category, page = 1, pageSize = 20) => 
    cloudRequest.callFn('fn-music', { action: 'list', category, page, pageSize }),
  
  // 
  getDetail: (id) => cloudRequest.callFn('fn-music', { action: 'detail', id })
};

export const cdkAPI = {
  // CDK
  redeem: (code) => cloudRequest.callFn('cdk-redeem', { code }, {
    showLoading: true,
    loadingText: '...'
  })
};

// 
export { cloudRequest, httpRequest };
export default { cloudRequest, httpRequest, authAPI, musicAPI, cdkAPI };